import React from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

const MainTopNavigationBar = ({ isLoggedIn, setIsLoggedIn }) => {
  
  const logout = async () => {
    try {
      //    .
      await axios.get("/auth/logout");

      //   .
      setIsLoggedIn(false);
    } catch (error) {
      console.error(' :', error);
    }
  }

  return (
    <div className="navigation">
      {isLoggedIn && <Link to="/posts"><input type="button" value={` `} /></Link>}
      {!isLoggedIn && <Link to="/auth/login"><input type="button" value={``} /></Link>}
      {isLoggedIn && <input type="button" value={``} onClick={logout} />}
    </div>
  );
};

export default MainTopNavigationBar;
